<template>
    <div id="problem">
        <loadings ref="load"></loadings>
        <navbar :isActive="page_belong"></navbar>
        <div class="container mundb-standard-container">
            <div class="row">
                <div class="col-sm-12 col-lg-9">
                    <paper-card class="animated fadeInLeft p-5" style="min-height: 643px;">
                        <div style="font-family: 'Consolas';white-space: pre;">
                            <div style="text-align: center"><h3>{{problem.problemId + ': ' + problem.title}}</h3></div>
                            <div style="text-align: center;margin-top: 10px;">
                                <span data-placement="left" data-toggle="tooltip" data-original-title="Time Limit">
                                    <i class="fa fa-clock-o" aria-hidden="true"></i>
                                    <span>
                                        <span fd="time_limit" pid="">{{' '}}{{problem.timeLimit}} ms{{" "}}{{" "}}{{" "}}{{" "}}</span>
                                    </span>
                                </span>
                                <span data-placement="left" data-toggle="tooltip" data-original-title="Memory Limit">
                                    <i class="fa fa-cog" aria-hidden="true"></i>
                                    <span>{{' '}}{{problem.memoryLimit}} MB{{" "}}{{" "}}{{" "}}{{" "}}</span>
                                </span>
                                <span>
                                    <i class="fa fa-sign-out" aria-hidden="true"></i>
                                    <span> Standard I/O</span>
                                </span>
                            </div>
                            <div v-if="problem.isSpj" style="text-align: center;color: red;margin-top: 10px"><span>Special Judge</span>
                            </div>
                        </div>
                        <div style="margin-top: 1rem;font-size: 17px">
                            <i class="fa fa-bars" aria-hidden="true"></i>
                            <span> Problem Information</span>
                        </div>
                        <div class="problem">
                            <div v-if="problem.problemDescribe != null && problem.problemDescribe != ''" class="card">
                                <div class="card-header" style="color: black">
                                    <span>Problem Description</span>
                                </div>
                                <div class="card-body">
                                    <span style="white-space: pre;" v-html="problem.problemDescribe"></span>
                                </div>
                            </div>
                            <div v-if="problem.inputDescribe != null && problem.inputDescribe != ''" class="card">
                                <div class="card-header" style="color: black">
                                    <span>Input Description</span>
                                </div>
                                <div class="card-body">
                                    <span style="white-space: pre;" v-html="problem.inputDescribe"></span>
                                </div>
                            </div>
                            <div v-if="problem.outputDescribe != null && problem.outputDescribe != ''" class="card">
                                <div class="card-header" style="color: black">
                                    <span>Output Description</span>
                                </div>
                                <div class="card-body">
                                    <span style="white-space: pre;" v-html="problem.outputDescribe"></span>
                                </div>
                            </div>
                            <template v-for="(sample,index) in problem.sampleIO">
                                <div class="double-card" v-bind:key="key">
                                    <div class="card" style="width: 48%;float: left;">
                                        <div class="card-header" style="color: black">
                                            <span>
                                                Sample Input&nbsp;{{index+1}}&nbsp;
                                                <a v-if="copy.id == index && copy.ing == true && copy.type == 0"
                                                   href="javascript:void(0)">
                                                    <i class="fa fa-check-circle" aria-hidden="true"></i>
                                                </a>
                                                <a v-else href="javascript:void(0)"
                                                   @click="handleCopy(sample.sample_I, $event, index, 0)">
                                                    <i class="fa fa-files-o" aria-hidden="true"></i>
                                                </a>
                                            </span>
                                        </div>
                                        <div class="card-body">
                                            <span style="white-space: pre;" v-bind:id="'sampleinput'+index"
                                                  class="sampledata">{{sample.sample_I}}</span>
                                        </div>
                                    </div>
                                    <div class="card" style="width: 48%;float: right;">
                                        <div class="card-header" style="color: black">
                                            <span>
                                                Sample Output&nbsp;{{index+1}}&nbsp;
                                                <a v-if="copy.id == index && copy.ing == true && copy.type == 1"
                                                   href="javascript:void(0)">
                                                    <i class="fa fa-check-circle" aria-hidden="true"></i>
                                                </a>
                                                <a v-else href="javascript:void(0)"
                                                   @click="handleCopy(sample.sample_O, $event, index, 1)">
                                                    <i class="fa fa-files-o" aria-hidden="true"></i>
                                                </a>
                                            </span>
                                        </div>
                                        <div class="card-body">
                                            <span style="white-space: pre;" v-bind:id="'sampleoutput'+index"
                                                  class="sampledata">{{sample.sample_O}}</span>
                                        </div>
                                    </div>
                                    <div style="clear: both"></div>
                                </div>
                            </template>
                            <div v-if="problem.hint != null && problem.hint != ''" class="card">
                                <div class="card-header" style="color: black">
                                    <span>Hint</span>
                                </div>
                                <div class="card-body">
                                    <span style="white-space: pre;" v-html="problem.hint"></span>
                                </div>
                            </div>
                        </div>
                    </paper-card>
                </div>
                <div class="col-sm-12 col-lg-3 no-print">
                    <paper-card class="animated fadeInRight btn-group-vertical cm-action-group">
                        <button type="button" class="btn btn-secondary submit-code" v-bind:onclick="'window.location.href=\'/submitpage.html?pid=' + problem.problemId + '\''">
                            <i class="fa fa-paper-plane fa-fw" aria-hidden="true"></i>
                            <template v-if="page_setting.is_login == false">
                                login & submit
                            </template>
                            <template v-else>
                                submit
                            </template>
                        </button>
                        <separate-line class="ultra-thin"></separate-line>

                        <button type="button" class="btn btn-secondary submit-info"
                                v-bind:onclick="'window.location.href=\'/status.html?pid=' + problem.problemId + '\''">
                            <!--<a v-bind:href="'' + problem.problemId">-->
                            <i class="fa fa-check-square fa-fw" aria-hidden="true"></i>
                            submited info
                            <!--</a>-->
                        </button>
                        <button type="button" class="btn btn-secondary data">
                            <i class="fa fa-code fa-fw" aria-hidden="true"></i>
                            source data
                        </button>
                    </paper-card>
                    <paper-card class="animated fadeInRight" id="RightInfoPanel">
                        <p><span>Info{{' '}}{{' '}}</span></p>
                        <div>
                            <a href="javascript:void(0)" target="_blank">
                                <img src="https://oj.ctguacm.work/image/ojlogo.png" alt="CTGUACMOJ"
                                     class="img-fluid mb-3">
                            </a>
                        </div>
                        <p>
                            <span s>Code{{' '}}{{' '}}</span>
                            <span class="wemd-black-text">CTGAOJ{{problem.problemId}}</span>
                        </p>
                        <p class="mb-0">
                            <span>Provider{{' '}}{{' '}}</span>
                        </p>
                        <div id="ProblemCategory" style="">
                            <a v-for="category in problem.category"
                               v-bind:style="{color: random_color(), 'border-color': info_color}"
                               v-bind:href="'/problemset.html?category=' + encodeURIComponent(category.cateId)"
                               v-bind:key="key">
                                {{category.name}}
                            </a>
                        </div>
                        <separate-line class="ultra-thin mb-3 mt-3"></separate-line>
                        <p class="mb-0">
                            <span>Tags{{' '}}{{' '}}</span>
                        </p>
                        <div id="ProblemSource" style="">
                            <a v-for="source in problem.source"
                               v-bind:style="{color: random_color(), 'border-color': info_color}"
                               v-bind:href="'/problemset.html?source=' + encodeURIComponent(source.sourceId)"
                               v-bind:key="key">
                                {{source.name}}
                            </a>
                        </div>
                        <p></p>
                        <p>
                            <span>Submitted{{' '}}{{' '}}</span>
                            <span class="wemd-black-text">{{problem.submitStatistics.allSubmitTimes}}</span>
                        </p>
                        <p>
                            <span>Solved{{' '}}{{' '}}</span>
                            <span class="wemd-black-text">{{problem.submitStatistics.acSubmitTimes}}</span>
                        </p>
                        <p>
                            <span>AC rate{{' '}}{{' '}}</span>
                            <span class="wemd-black-text">{{(problem.submitStatistics.allSubmitTimes == 0)? '---': (100*problem.submitStatistics.acSubmitTimes/problem.submitStatistics.allSubmitTimes).toFixed(2) + "%"}}</span>
                        </p>
                        <p>
                            <span>Date{{' '}}{{' '}}</span>
                            <span class="wemd-black-text">{{format_time(problem.createTime)}}</span>
                        </p>
                    </paper-card>
                </div>
            </div>
        </div>
        <footers></footers>
    </div>
</template>

<script>
    /* eslint vue/valid-v-for: "off"*/
    /* eslint no-unused-vars: "off"*/
    import navBar from "@/components/navbar/navbar";
    import fun from '@/assets/js/OJFunction'
    import Footers from "@/components/footer/footer";
    import Loadings from "@/components/loading/loading";
    import Clipboard from 'clipboard'
    // ,
    export default {
        components: {
            Loadings,
            Footers,
            'navbar': navBar
        },
        name: "problem",
        data() {
            return {
                page_belong: 'problemset',
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    language: "",
                    pid: 0,
                    is_login: false
                },
                problem: "",
                copy: {
                    id: "",
                    ing: false,
                    type: "",
                },
                info_color: ''
            }
        },
        methods: {
            format_time: function (time) {
                return fun.format_time(time);
            },
            handleCopy(text, event, i, j) {
                fun.handleClipboard(text, event)
                this.copy.id = i;
                this.copy.type = j;
                this.copy.ing = true;
                var _this = this;
                setTimeout(function () {
                    _this.copy.ing = false;
                    // alert(1);
                }, 500)
            },
            test: function () {
                alert(this.$refs.sample0);
            },
            copyLink(data) {
                console.log(1)
                let clipboard = new Clipboard('.tag')

                clipboard.on('success', e => {
                    alert(2)
                    // 释放内存
                    clipboard.destroy()
                })
                clipboard.on('error', e => {
                    alert(5)
                    clipboard.destroy()
                })
            },
            random_color: function () {
                var color_theme = new Array("#F44336", "#ff9800", "#4CAF50", "#8bc34a", "#00bcd4", "#2196F3", "#9c27b0", "#3f51b5", "#009688", "#e91e63");
                var x = Math.floor(Math.random() * color_theme.length);
                return this.info_color = color_theme[x];
            }
        },
        created() {
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");
            console.log(this.page_setting.access_token);
            var lang = localStorage.getItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang");
            if (lang != null) {
                this.page_setting.language = lang;
            }
            this.page_setting.pid = fun.getUrlKey("pid");
            if (this.page_setting.pid == null) window.location.href = this.GLOBAL.OJ_DEFAULT_FRONT_SERVE + "/problemset.html"
            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/problem',
                this.page_setting,
                {emulateJSON: true}).then(function (res) {
                console.log(res);
                var error_type = res.body.OJ_ERROR_TYPE;
                if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                    this.page_setting.is_login = true;
                    // if(res.body.OJ_USER_INFO.)
                    var remember_me = res.body.OJ_USER_INFO.saved;
                    var access_token = res.body.OJ_USER_INFO.access_token;
                    var refresh_token = res.body.OJ_USER_INFO.refresh_token;
                    if (res.body.OJ_USER_INFO.permissions.admin || res.body.OJ_USER_INFO.permissions.superAdmin) {
                        fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_ADMIN);
                    } else {
                        fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_USER);
                    }
                    if (remember_me) {
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                    } else {
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                    }
                } else if (error_type == this.GLOBAL.OJ_ERROR_NON_EXISTENT_PROBLEM) {
                    window.location.href = '/problemset.html';
                } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                    fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_VISITOR);
                }
                this.problem = res.body.OJ_PROBLEM;
                document.title = this.problem.title + " | CTGUACM";

                lang = res.body.OJ_LANGUAGE_TYPE;
                localStorage.setItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang", lang);
                this.$i18n.locale = lang;
                $(document).ready(function () {
                    $(".double-card").each(function () {
                        var height1 = $(this).children(".card:first").children(".card-body").height();
                        var height2 = $(this).children(".card:last").children(".card-body").height();
                        var max_height;
                        if (height1 > height2) max_height = height1;
                        else max_height = height2;
                        $(this).children(".card:first").children(".card-body").css("height", (max_height + 25) + "px");
                        $(this).children(".card:last").children(".card-body").css("height", (max_height + 25) + "px");
                    });
                });
                // alert(this.max_page);
            });

        }
    }

</script>

<style scoped lang="scss">
    #problem {
        #ProblemSource, #ProblemCategory {
            margin-top: 5px;
            a {
                display: inline-block;
                border: 1.5px solid #e91e63;
                border-radius: 4px;
                padding: 3px 5px 3px 5px;
                text-decoration: none;
                font-size: 0.855rem;
                font-weight: bolder;
                margin: 2px
            }
        }
        #RightInfoPanel span {
            white-space: pre
        }
        separate-line {
            display: block;
            margin: 0;
            padding: 0;
            height: 1px;
            width: 100%;
            background: rgba(0, 0, 0, 0.25);
        }
        separate-line.ultra-thin {
            transform: scaleY(0.5);
        }
        .cm-action-group > button {
            text-align: left !important;
            margin: .3125rem 0 !important;
            border-radius: 0 !important;
        }
        .cm-action-group {
            margin: 0 !important;
            margin-bottom: 2rem !important;
            padding: 0 !important;
            display: flex !important;
        }
        .problem > .card:not(:first-of-type), .problem > .double-card {
            margin-top: 40px !important;
        }
        .double-card > .card > .card-body::-webkit-scrollbar {
            width: 0px;
            height: 8px;
            border-radius: 4px;
        }
        .double-card > .card > .card-body::-webkit-scrollbar-thumb {
            background-color: rgb(163, 163, 163);
        }
        .double-card > .card > .card-body {
            overflow-x: scroll;
            overflow-y: hidden;
        }
        .double-card .card {
            margin-top: 0px !important;
            margin-bottom: 0px !important;
        }
        .double-card .card .card-body {
            font-family: 'Consolas' !important;
        }
        .card .card-body {
            font-family: 'Roboto-slab' !important;
            padding: 14px !important;
            font-size: 16px !important;
            color: rgb(46, 46, 46);
        }
        .card .card-header {
            padding: 11px 14px 11px 14px !important;
            font-family: 'Roboto-slab' !important;
            font-weight: bold !important;
            font-size: 17px !important;
            background-color: rgb(233, 233, 233) !important;
            border-radius: 5px 5px 0px 0px !important;
            color: black;
        }
        .double-card .card {
            margin-top: 0px !important;
            margin-bottom: 0px !important;
        }
        .card {
            margin-top: 50px;
            margin-bottom: 20vh;
            /* overflow: hidden; */
            display: block;
            box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 30px;
            border-radius: 4px;
            transition: .2s ease-out .0s;
            color: #7a8e97;
            background: #fff;
            position: relative;
            border: 1px solid rgba(0, 0, 0, 0.15);
            /* width: calc( 15% + 250px); */
            left: auto;
        }
        .card {
            margin-top: 20px !important;
            margin-bottom: 20px !important;
            border-radius: 5px !important;
            border: none !important;
        }
        .p-5 {
            padding: 3rem !important;
        }
        paper-card {
            display: block;
            box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 30px;
            border-radius: 4px;
            transition: .2s ease-out .0s;
            color: #7a8e97;
            background: #fff;
            padding: 1rem;
            position: relative;
            border: 1px solid rgba(0, 0, 0, 0.15);
            margin-bottom: 2rem;
        }
    }
</style>